/*------------------------------ <BUTTONS> ------------------------------*/
.btn {
  display: inline-block;
  padding: 0 42px;
  height: 47px;
  line-height: 44px;
  text-transform: uppercase;
  font-size: 16px;
  letter-spacing: 0.67px;
  position: relative; }
  .btn:after {
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    transform: scale(0);
    transition: all 0.3s ease 0; }
  .btn_w {
    color: #fff;
    border: 1px solid #fff; }
    .btn_w:after {
      background-color: #fff; }
  .btn_b {
    color: #000;
    border: 1px solid #000; }
    .btn_b:after {
      background-color: #000; }
  .btn span {
    transition: all 0.3s ease 0s;
    position: relative;
    z-index: 1; }
  .btn:hover.btn_w {
    color: #000; }
  .btn:hover.btn_b {
    color: #fff; }
  .btn:hover:after {
    transform: scale(1); }

/*------------------------------ </BUTTONS> ------------------------------*/
/*------------------------------ <HEADER-BLOCKS> ------------------------------*/
.header-block {
  text-align: center; }
  .header-block__title {
    font-size: 34px;
