/*------------------------------ <MAINBLOCK> ------------------------------*/
.mainblock {
  flex: 1 1 auto;
  position: relative;
  display: flex;
  font-size: 0;
  justify-content: center;
  text-align: center;
  padding: 50px 0; }
  @media screen and (max-width: 767.98px), screen and (max-height: 600px) {
