      flex-direction: column;
      justify-content: space-between;
      align-items: center; }
  .mainblock__text {
    color: #ffffff;
    position: relative;
    padding: 0 0 45px 0;
    margin: 30px 0; }
    .mainblock__text:after {
      content: '';
      width: 105px;
      height: 2px;
      background-color: #fff;
      position: absolute;
      left: 50%;
      margin: 0 0 0 -52.5px;
      bottom: 0; }
  .mainblock__title {
    font-weight: 900;
    font-size: 34px;
    line-height: 40px;
    text-transform: uppercase;
    letter-spacing: 5.43px;
    margin: 0 0 10px 0; }
    @media screen and (max-width: 767.98px), screen and (max-height: 600px) {
      .mainblock__title {
        font-size: 25px;
        line-height: 30px; } }
  .mainblock__subtitle {
