  .mainblock__footer {
    display: inline-flex;
    flex-direction: column; }
  .mainblock__btn {
    margin: 0 0 60px 0; }
    @media screen and (max-width: 767.98px), screen and (max-height: 600px) {
      .mainblock__btn {
        margin: 0 0 30px 0; } }
  .mainblock__arrow {
    position: relative;
    animation: arrow 2s infinite ease; }
  .mainblock__image {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0; }

@keyframes arrow {
  0% {
    top: 0; }
  50% {
    top: 10px; }
  100% {
    top: 0; } }

